.home-button {
  padding: 0 20px 3px 20px;
  height: 45px;
  font-size: 20px;
  color: var(--black);
  border: 3px solid var(--black);
  border-radius: 10px;
  font-weight: 700;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  background-color: var(--white);
  opacity: 1;
}

.home-button:hover {
  transform: translateY(-5px);
}

.home-auth-container {
  position: fixed;
  top: 20px;
  right: 20px;
  display: flex;
  gap: 10px;
  z-index: 100;
}

.home-link {
  text-decoration: none;
}

.home-welcome-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: auto;
  padding: 40px 20px 20px 20px;
  margin-top: 300px;
  position: relative;
  z-index: 10;
}

.home-welcome-text {
  font-size: 48px;
  font-weight: 700;
  color: var(--yellow);
  text-align: center;
  margin: 0;
  text-shadow: 3px 3px 0px var(--black);
}

.home-grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 20px;
  padding: 20px;
  max-width: 1200px;
  margin: 200px auto 0 auto;
  position: relative;
  z-index: 10;
}

.home-grid-box {
  background-color: var(--white);
  border-radius: 15px;
  aspect-ratio: 1;
  border: 3px solid var(--black);
  opacity: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.grid-box-label {
  font-size: 18px;
  font-weight: 700;
  color: var(--black);
  text-align: center;
  padding: 10px;
}

/* Cloud animation container */
.cloud-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
  z-index: 1;
}

/* Individual cloud styling and animation */
.animated-cloud {
  position: absolute;
  width: auto;
  height: auto;
  will-change: transform; /* Optimize for smooth animation */
  animation: cloudMove linear infinite;
  animation-fill-mode: both; /* Keep initial and final states */
  left: -500px; /* Start far off-screen to the left */
}

@keyframes cloudMove {
  0% {
    transform: translateX(0);
    opacity: 0; /* Fade in */
  }
  2% {
    opacity: 1; /* Fully visible */
  }
  98% {
    opacity: 1; /* Stay visible */
  }
  100% {
    transform: translateX(calc(100vw + 500px)); /* Move to off-screen right */
    opacity: 0; /* Fade out */
  }
}

/* Modal overlay for the white square */
.square-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* White square modal content */
.square-modal-content {
  width: 800px;
  height: 700px;
  background-color: var(--primary-blue);
  border: 4px solid var(--black);
  border-radius: 15px;
  position: relative;
}

/* Close button (X) in top right corner */
.modal-close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  font-size: 32px;
  font-weight: 700;
  color: var(--black);
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1001;
  line-height: 1;
  padding: 0;
}


